            "" /* å çºä¸éå§Useræ¯ç©ºå¼ï¼è£¡é¢çnameæundefineï¼æä»¥åçµ¦nameä¸åç©ºå¼ */,
          avatar: "",
        },
      },
      comment: "",
      isLike: false,
      isLoading: true,
      isProcessing: false,
      isLikePorcessing: false,
      topUsers: [],
      likeï¼£ount: 0,
    };
  },
  created() {
    const currentPath = this.$router.history.current.name;
    if (currentPath === "ReplyList") {
      this.MainPage = true;
      this.isSetting = false;
      this.PersonalInfo = false;
      this.ChatRoom = false;
    }
    const { id: tweetId } = this.$route.params; /* TODO: è§£æ§ä»å¼å */
    this.fetchReplyList({ tweetId });
    this.fetchTopUsers();
  },
  methods: {
    async fetchTopUsers() {
      try {
        this.isLoading = true;
        const { data } = await mainPageAPI.getUsersTop();

        this.topUsers = data;

        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        console.log("error", error);
        Toast.fire({
          icon: "error",
          title: "è¼å¥è³æå¤±æï¼è«ç¨å¾åè©¦",
        });
      }
    },
    async fetchReplyList({ tweetId }) {
      try {
        this.isLoading = true;
        const response = await mainPageAPI.ReplyList({ tweetId });
        this.tweetData = { ...response.data };
        console.log("this.tweetData", this.tweetData);
        this.likeï¼£ount = this.tweetData.Likes.length;
        // æª¢é©ä½¿ç¨èæ¯å¦æå°è²¼ææè®
        if (this.tweetData.isLikedByMe) {
