        }
        this.likeï¼£ount = this.likeï¼£ount - 1;
        this.isLikePorcessing = false;
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "ç¡æ³å°æ¨æç§»é¤ææï¼è«ç¨å¾åè©¦",
        });
        this.isLikePorcessing = false;
      }
    },
  },
  computed: {
    ...mapState([
