      "isAuthenticated",
    ]) /* TODO: åæ¯è§£æ§ä»å¼è¦å */,
  },
  filters: {
    publishTime(datetime) {
      if (!datetime) {
        return "-";
      }
      // ä½¿ç¨ moment æä¾ç fromNow æ¹æ³
      return moment(datetime).format("h:mm:ss aï¼MMMM Do YYYY");
    },
    fromNow(datetime) {
      if (!datetime) {
        return "-";
      }
      // ä½¿ç¨ moment æä¾ç fromNow æ¹æ³
      return moment(datetime).fromNow();
    },
  },
};
